:root {
  --pri-font: "Nunito Sans", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --dark-blue: hsl(209, 23%, 22%);
  --very-dark-blue: hsl(207, 26%, 17%);
  --dark-gray: hsl(0, 0%, 52%);
  --very-light-gray: hsl(0, 0%, 98%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--pri-font);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 0.15s ease-in-out forwards;
}

body {
  background-color: var(--very-light-gray);
}

.navbar {
  background-color: var(--white);
  box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.15);
}

.topElementsCtn form input {
  border: 1px solid #fff;
}
.topElementsCtn form input:focus {
  outline: none;
  border: 1px solid var(--very-dark-blue);
}
.topElementsCtn .filter {
  transition: all 0.07s ease-in-out !important;
  border: 1px solid #fff;
}
.topElementsCtn .filter.active {
  border: 1px solid var(--very-dark-blue);
}
.topElementsCtn ul {
  transition: all 0.07s ease-in-out !important;
}
.topElementsCtn ul li {
  transition: all 0.07s ease-in-out !important;
}
.topElementsCtn ul li:hover {
  cursor: pointer;
  background-color: var(--very-light-gray);
}

.countries__ctn {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
}
@media (max-width: 63.9375em) {
  .countries__ctn {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 39.937em) {
  .countries__ctn {
    grid-template-columns: 1fr;
  }
}
.countries__ctn .card {
  cursor: pointer !important;
  transition: all 0.07s ease-in-out !important;
}
.countries__ctn .card:hover {
  transform: scale(1.05) !important;
}

body {
  transition: all 0.07s ease-in-out !important;
}
body.dark {
  color: #fff !important;
  background-color: var(--very-dark-blue) !important;
}
body.dark .navbar {
  background-color: var(--dark-blue) !important;
}
body.dark .topElementsCtn form input {
  background-color: var(--dark-blue) !important;
  border: none !important;
}
body.dark .topElementsCtn form input:focus {
  border: 1px solid var(--very-light-gray) !important;
}
body.dark .topElementsCtn .filter_ctn .filter {
  background-color: var(--dark-blue) !important;
  border: 1px solid var(--dark-blue) !important;
}
body.dark .topElementsCtn .filter_ctn .filter.active {
  border: 1px solid var(--very-light-gray) !important;
}
body.dark .topElementsCtn .filter_ctn .list {
  background-color: var(--dark-blue) !important;
}
body.dark .topElementsCtn .filter_ctn .list li:hover {
  background-color: var(--very-dark-blue);
}
body.dark .countries__ctn .card {
  background-color: var(--dark-blue) !important;
}
body.dark button {
  background-color: var(--dark-blue) !important;
}

.transition {
  transition: all 0.15s ease-in !important;
}

